import React from "react";
import Style from "../css/ResetPassword.module.css";
function ResetPassword() {
  return (
    <div className={Style.ResetWrapper}>
      <form>
        <p>E-mail</p>
        <input type="email" placeholder="Example@E-mail.com" required />
        <input type="submit" value="Send me Code" />
      </form>
    </div>
  );
}

export default ResetPassword;
